@import "@fontsource/mali";
@import "@fontsource/lobster";

.mainContainer {
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(to bottom right, #00728B, #055AFF);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.yesnoText {
  font-size: 256px;
  width: 100%;
  height: 40%;
  margin: 0;
  padding: 0;
  padding-top: 5%;
  text-align: center;
  color: white;
  font-family: "Lobster";
}

.subtext {
  font-size: 96px;
  width: 100%;
  height: 40%;
  margin: 0;
  padding: 0;
  color: white;
  text-align: center;
  font-family: "Mali";
}